.page-header {
  position: relative;
  min-height: 239px;
  overflow: hidden;
}
.page-header::before {
  position: absolute;
  content: "";
  display: block;
  min-height: 239px;
  left: 0;
  right: 0;
  bottom: 0;
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../img/bg-back-mobile@1x.jpg");
}
