  }
}
.page-header--main {
  min-height: 734px;
}
.page-header--main::before {
  bottom: 264px;
  min-height: 470px;
  background-position: center;
}
.page-header__wrapper {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  opacity: 0.99;
}
.page-header__inner {
  width: 100%;
