}
.page-header__logo:not(.page-header__logo--main):hover path,
.page-header__logo:not(.page-header__logo--main):focus path {
  fill: #d22856;
}
.page-header__logo:not(.page-header__logo--main):active path {
  fill: #ffffff;
  opacity: 0.3;
}
.page-header__logo-icon:active {
  opacity: 0.3;
}
.page-header__logo-icon:active path {
  fill: #ffffff;
}
.page-header__logo-icon {
  width: 75px;
  height: 22px;
  pointer-events: none;
}
.page-header__intro {
  position: relative;
  z-index: 2;
}
.page-header__title {
  display: block;
