    display: none;
  }
  .page-header--main::before {
    top: 0;
    min-height: 729px;
  }
  .page-header__inner {
    position: relative;
    z-index: auto;
  }
  .page-header__inner::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: -1;
    min-height: 729px;
    background-position: center;
