.main-nav {
  position: relative;
  font-family: inherit;
  font-size: 18px;
  font-weight: 700;
  word-wrap: break-word;
  line-height: 32px;
  color: #ffffff;
  text-transform: uppercase;
}
.main-nav__toggle {
  position: absolute;
  right: 20px;
  top: -48px;
  z-index: 1;
  width: 50px;
  height: 24px;
  padding: 0;
  font-size: 0;
  background-color: transparent;
  cursor: pointer;
  border: 0;
  outline: none;
}
.main-nav__toggle-item {
  position: absolute;
  display: block;
  top: 0;
  right: 0;
  width: 100%;
  height: 4px;
  background-color: #ffffff;
}
.main-nav__toggle-item::before,
.main-nav__toggle-item::after {
  content: "";
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 4px;
  left: 0;
  background-color: #ffffff;
}
.main-nav--closed .main-nav__toggle:hover .main-nav__toggle-item,
.main-nav--closed .main-nav__toggle:focus .main-nav__toggle-item {
  background-color: #d22856;
}
.main-nav--closed .main-nav__toggle:hover .main-nav__toggle-item::before,
.main-nav--closed .main-nav__toggle:focus .main-nav__toggle-item::before,
.main-nav--closed .main-nav__toggle:hover .main-nav__toggle-item::after,
.main-nav--closed .main-nav__toggle:focus .main-nav__toggle-item::after {
  background-color: #d22856;
}
.main-nav--closed .main-nav__toggle:active .main-nav__toggle-item {
  background-color: #d22856;
  opacity: 0.3;
}
.main-nav--closed .main-nav__toggle:active .main-nav__toggle-item::before,
.main-nav--closed .main-nav__toggle:active .main-nav__toggle-item::after {
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:hover .main-nav__toggle-item::before,
.main-nav--opened .main-nav__toggle:focus .main-nav__toggle-item::before,
.main-nav--opened .main-nav__toggle:hover .main-nav__toggle-item::after,
.main-nav--opened .main-nav__toggle:focus .main-nav__toggle-item::after {
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:active .main-nav__toggle-item {
  opacity: 0.3;
}
.main-nav--opened .main-nav__toggle:active .main-nav__toggle-item::before,
.main-nav--opened .main-nav__toggle:active .main-nav__toggle-item::after {
  background-color: #d22856;
}
.main-nav--closed .main-nav__wrapper {
  display: none;
}
.main-nav--closed .main-nav__toggle-item::before {
  top: 10px;
}
.main-nav--closed .main-nav__toggle-item::after {
  top: 20px;
}
.main-nav--opened .main-nav__wrapper {
  display: block;
}
.main-nav--nojs .main-nav__toggle-item {
  display: none;
}
.main-nav--nojs .main-nav__wrapper {
  display: block;
}
.main-nav--opened .main-nav__toggle-item {
  top: 10px;
  right: 4px;
  width: 23px;
  background-color: transparent;
}
.main-nav--opened .main-nav__toggle-item::before {
  width: 30px;
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle-item::after {
  width: 30px;
  transform: rotate(-45deg);
}
@media (min-width: 660px) {
  .main-nav__toggle {
    right: 20px;
    top: -65px;
  }
}
@media (min-width: 960px) {
  .main-nav {
    line-height: 18px;
    font-weight: 400;
    text-transform: capitalize;
  }
  .main-nav--closed .main-nav__wrapper {
    display: block;
  }
  .main-nav__toggle {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
