.edit {
  padding-bottom: 42px;
}
.edit__list {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 215px;
  margin: 0;
  padding: 0;
  font-family: inherit;
  list-style: none;
  margin: 0 auto;
  margin-top: 32px;
  padding-bottom: 65px;
  padding-right: 16px;
}
.edit__item {
  position: relative;
}
.edit__range {
  position: absolute;
  z-index: 1;
  display: block;
  width: 240px;
  left: -15px;
  top: 85px;
  margin-bottom: 35px;
  padding-top: 0;
  background-color: transparent;
  outline: none;
  appearance: none;
}
.edit__range--contrast,
.edit__range--fill {
  display: none;
}
.edit__range::-webkit-slider-runnable-track {
  width: 100%;
  height: 2px;
  background-color: #283645;
  border: 0;
}
.edit__range::-webkit-slider-thumb {
  width: 14px;
  height: 14px;
  margin-top: -6px;
  border: 2px solid #d22856;
  border-radius: 50%;
  background-color: #ffffff;
  cursor: pointer;
  appearance: none;
}
.edit__range::-webkit-slider-thumb:active {
  background-color: #d22856;
  outline: none;
}
.edit__range::-ms-thumb {
  display: block;
  width: 14px;
  height: 14px;
  margin-top: 0;
  background-color: #ffffff;
  border: 2px solid #d22856;
  border-radius: 50%;
  box-sizing: border-box;
  cursor: pointer;
  appearance: none;
}
.edit__range::-ms-thumb:active {
  background-color: #d22856;
  outline: none;
}
.edit__range::-ms-track {
  width: 100%;
  height: 2px;
  border-width: 7px 0;
  background-color: transparent;
  border-color: transparent;
  color: transparent;
  cursor: pointer;
}
.edit__range::-ms-fill-lower {
  background-color: #000000;
}
.edit__range::-ms-fill-upper {
  background-color: #000000;
}
.edit__range::-moz-range-track {
  width: 100%;
  height: 2px;
  background-color: transparent;
  border-color: transparent;
  color: transparent;
  cursor: pointer;
}
.edit__range::-moz-range-thumb {
  width: 14px;
  height: 14px;
  margin-top: 0;
  background-color: #ffffff;
  border: 2px solid #d22856;
  border-radius: 50%;
  box-sizing: border-box;
  cursor: pointer;
  appearance: none;
}
.edit__range:-moz-focusring {
  outline: 1px solid white;
  outline-offset: -1px;
}
.edit__icon {
  padding: 0;
  margin: 0;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
}
.edit__icon--crop {
  width: 48px;
  height: 48px;
}
.edit__icon--fill {
  width: 48px;
  height: 48px;
}
.edit__icon--contrast {
  width: 32px;
  height: 32px;
}
.edit__img {
  opacity: 0.3;
}
.edit__img--crop {
  width: 100%;
  height: 100%;
}
.edit__img--fill {
  width: 100%;
  height: 100%;
}
.edit__img--contrast {
  width: 100%;
  height: 100%;
}
.edit__img--current,
.edit__img:active {
  opacity: 1;
  fill: #d22856;
}
.edit__img:hover,
.edit__img:focus,
.edit__img:hover rect,
.edit__img:focus rect {
  opacity: 1;
}
.edit__button-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 35px;
  border-top: 2px solid #dddfe0;
}
.edit__button {
  width: 280px;
