}
.edit__button--red:hover,
.edit__button--red:focus {
  color: #ffffff;
  background-color: #d22856;
}
.edit__button--red:active {
  color: #e07e94;
}
.edit__button--grey {
  color: #283645;
  border-color: #283645;
  opacity: 0.1;
}
.edit__button--grey:hover,
.edit__button--grey:focus {
  opacity: 0.3;
}
.edit__button--grey:active {
  color: #ffffff;
  border-color: #283645;
