.contacts {
  z-index: 1;
}

.contacts__map {
  width: 100%;
  top: 0;
  min-height: 245px;
}

.contacts__map-img {
  height: 100%;
  width: 100%;
}

.contacts__wrapper {
  position: relative;
  z-index: 1;
  width: 100%;

  background-color: #ffffff;

  text-align: center;

  &::after {
    position: absolute;
    z-index: -1;
    display: block;
    width: 100%;
    min-height: 75px;
    top: 100%;
    bottom: auto;


    content: "";
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 595.28 61.905' version='1.0' xmlns='http://www.w3.org/2000/svg' width='595.28' height='61.905'%3E%3Cpath fill='%23fff' d='M297.64 61.905L0 0h595.28z'/%3E%3C/svg%3E%0A");
    background-position: 50% 100%;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.contacts__title {
  margin: 0;
  padding-top: 42px;
  padding-bottom: 15px;

  .title();
  line-height: 30px;
}

.contacts__list {
  padding-top: 35px;
  padding-bottom: 35px;

  .list();
}

.contacts__item {
  padding-top: 13px;
}

.contacts__text {
  .subtitle();
  line-height: 32px;
  margin: 0;
}

.contacts__info {
  margin: 0;

  .title();
  line-height: 30px;
  color: @add-color;

  &:not(&--address)&:hover,
  &:not(&--address):focus {
    color: @base-color;
    text-decoration: underline;
  }

  &:not(&--address):active {
    text-decoration: none;
    opacity: 0.3;
  }
}

@media (min-width: @tablet-width) {
  .contacts__wrapper {
    &::after {
      min-height: 125px;
    }
  }

  .contacts__title {
    display: inline-block;
    width: 400px;

    padding-top: 45px;
    margin: 0 auto;

    font-size: 40px;
    line-height: 50px;
  }

  .contacts__list {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    flex-grow: 1;
  }

  .contacts__item {
    width: 300px;
    padding-top: 22px;
    padding-left: 25px;
    padding-right: 25px;

    &:last-child {
      padding-top: 41px;
    }

    &--phone {
      padding-left: 14px;
    }

    &--email {
      padding-right: 14px;
    }
  }

  .contacts__map {
    min-height: 525px;
  }
}

@media (min-width: @desktop-width) {
  .contacts__wrapper {
    &::after {
      min-height: 150px;
    }
  }

  .contacts__title {
    width: 500px;
    padding-top: 65px;

    font-size: 50px;
    line-height: 60px;
  }

  .contacts__item {
    width: 33.33%;
    padding-top: 35px;

    &--phone {
      padding-right: 30px;
      order: 1;
    }

    &--email {
      padding-right: 10px;
      order: 3;
    }

    &--address {
      order: 2;
    }

    &:last-child {
      padding-top: 35px;
    }
  }

  .contacts__text {
    padding-bottom: 20px;
  }

  .contacts__map {
    min-height: 725px;
  }
}
